<script>
import { Splide, SplideSlide } from "@splidejs/vue-splide";
import "@splidejs/vue-splide/css";
export default {
	name: "ServiceCarousel",
	components: {
		Splide,
		SplideSlide,
	},
	data() {
		return {
			photos: [
				{
					img: "https://picsum.photos/300/300",
					text: 1,
				},
				{
					img: "https://picsum.photos/300/300",
					text: 2,
				},
				{
					img: "https://picsum.photos/300/300",
					text: 3,
				},
				{
