					text: 11,
				},
				{
					img: "https://picsum.photos/300/300",
					text: 12,
				},
			],
		};
	},
	computed: {
		checkCount() {
			if (this.photos.length <= 4) {
				return 1;
			} else if (this.photos.length > 5 && this.photos.length <= 8) {
				return 2;
			} else if (this.photos.length > 9 && this.photos.length <= 12) {
				return 3;
			} else {
				return 4;
			}
		},
	},
};
</script>

