<template>
	<div class="margin-block-start page-margin-inline">
		<div class="cart-wrapper">
			<h1 class="fs-1300">HOME / <span class="fw-bold">CART</span></h1>

			<div class="cart-columns">
				<div class="cart-left">
					<div class="cart-supplies">
						<h2 class="fs-900">Supplies</h2>
						<div class="cart-container" v-for="item in supplies" :key="item.id">
							<h3>{{ item.categoryName }}</h3>
							<div
								class="cart-container-item"
								v-for="product in item.categoryItems"
								:key="product.id"
							>
								<img :src="product.productImage" alt="" />
								<div class="cart-container-desc">
									<div class="cart-container-desc-top">
										<h4>
											{{ product.productName }}
										</h4>
										<p>Unit Price {{ product.unitPrice }}</p>
									</div>
									<p>Total Price {{ product.totalPrice }}</p>
								</div>
								<div class="cart-container-options">
									<div class="cart-container-selector">
										<div class="cart-container-qty">
											<div class="cart-qty-selector">
												<button>
													<i class="fa-solid fa-minus" />
												</button>
												<input type="text" value="1" />
												<button>
													<i class="fa-solid fa-plus" />
												</button>
											</div>
										</div>
										<!-- IF STATEMENT HERE WITH SUPPLY OF PRODUCT -->
										<span>Only 1 Item(s) left in stock</span>
									</div>

									<button>
										<img src="../assets/Delete.svg" alt="Delete Icon" />
									</button>
								</div>
							</div>
						</div>
					</div>
					<div class="cart-services">
						<h2 class="fs-900">Services</h2>
