									<div class="cart-container-desc-top">
										<h4>
											{{ product.productName }}
										</h4>
										<p>Unit Price {{ product.unitPrice }}</p>
									</div>
									<textarea name="" id="">Other Instructions: </textarea>
									<p>Total Price {{ product.totalPrice }}</p>
								</div>
								<div class="cart-container-options">
									<div class="cart-container-selector">
										<div class="cart-container-qty">
											<div class="cart-qty-selector">
												<button>
													<i class="fa-solid fa-minus" />
												</button>
												<input type="text" value="1" />
												<button>
													<i class="fa-solid fa-plus" />
												</button>
											</div>
										</div>
										<!-- IF STATEMENT HERE WITH SUPPLY OF PRODUCT -->
										<span>Only 1 Item(s) left in stock</span>
									</div>

									<button>
										<img src="../assets/Delete.svg" alt="Delete Icon" />
									</button>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="cart-right">
					<div class="order-summary-wrapper">
						<div class="order-summary-header">
							<h5>ORDER SUMMARY</h5>
						</div>
						<div class="order-summary-body">
							<div class="order-summary-total">
								<div class="order-summary-total-row">
									<p>Subtotal</p>
									<p>(0 items)</p>
								</div>
								<div class="order-summary-total-row">
									<p>Total</p>
									<p>P 0.0</p>
								</div>
								<div class="order-summary-shipping">
									<span>Shipping fee to be calculated on checkout</span>
								</div>
							</div>

							<div class="order-summary-voucher">
								<form action="/">
									<label for="voucher">Add Voucher Code</label>
									<input
										type="text"
										name="voucher"
										id="voucher"
										placeholder="Type Here..."
									/>
								</form>
							</div>

							<div class="order-summary-buttons">
								<button><router-link to="/checkout">Proceed to Checkout</router-link></button>

								<button aria-button="back-to-cart">
									<i class="fa-solid fa-circle-arrow-left"></i>
