<template>
	<div class="credit-card-popup">
		<div class="card-popup-inner">
			<div class="card-popup-header">
				<h1 class="fs-900">Add Credit/Debit Card</h1>
				<button class="cart-exit-button" @click="TogglePopup()">
					<i class="fa-solid fa-xmark fs-900"></i>
				</button>
			</div>
			<div class="card-popup-details">
				<h2>Card Details</h2>
				<div class="card-popup-inputs">
					<form action="/">
						<input
							type="text"
							placeholder="Card number"
							aria-area="top-corners"
						/>
						<div class="card-popup-inputs-mmcv">
							<input
								type="text"
								placeholder="MM / YY"
								aria-area="bottom-left"
							/>
							<input
								type="text"
								placeholder="CVC / CVV"
								aria-area="bottom-right"
							/>
						</div>
					</form>
				</div>
			</div>
			<div class="card-billing-address">
				<h2>Billing Address</h2>
				<section>
					<input
						type="checkbox"
						id="same-address"
						name="same-address"
						value="same-address"
					/>
					<label for="same-address">Same as Billing Address</label>
				</section>
			</div>
			<div class="card-popup-options">
				<button @click="TogglePopup()">Confirm</button>
				<button aria-cancel="true" @click="TogglePopup()">Cancel</button>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: "CardPopup",
	props: ["TogglePopup"],
};
</script>

<style lang="scss">
@import "../styles/global-variables.scss";

.credit-card-popup {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 99;
	background-color: rgba(0, 0, 0, 0.2);
	display: flex;
	align-items: center;
	justify-content: center;

	@media (max-width: 1230px) {
		.card-popup-inner {
			width: 70vw !important;
			height: fit-content !important;
		}

		.card-popup-options {
			justify-content: center !important;
		}
	}

	.card-popup-inner {
		background: #fff;
		width: 30vw;
		height: 50vh;
		padding: $size-600;

		.card-popup-header {
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-bottom: 1px solid $color-accent;
			padding-bottom: $size-100;

			h1 {
				color: #111827;
			}

			button {
				margin-top: 0px;
				color: $color-accent;
				border: none;
			}
		}

		.card-popup-details {
			margin-top: $size-300;
			h2 {
				color: #374151;
			}

			.card-popup-inputs {
				form {
					margin-top: $size-300;
					width: 100%;
				}

				input {
					width: 100%;
					padding: $size-100 $size-300;
					border: 1px solid $color-accent;
					color: #6b7280;

					&[aria-area="top-corners"] {
						border-radius: 10px 10px 0 0;
						border-bottom: none;
					}

					&[aria-area="bottom-left"] {
						border-radius: 0 0 0 10px;
						border-right: none;
					}

					&[aria-area="bottom-right"] {
						border-radius: 0 0 10px 0;
					}
				}

				.card-popup-inputs-mmcv {
					display: flex;
				}
			}
		}

		.card-billing-address {
			margin-top: $size-300;

			h2 {
				color: #374151;
			}

			label {
				position: relative;
				border-radius: 10px;
				padding: 1rem 2rem 1rem 0;
				width: 18rem;
				display: flex;
				align-items: center;
				color: rgba(0, 0, 0, 0.7);

				&:before {
					content: "";
					height: 20px;
					width: 20px;
					border: 1px solid $color-accent;
					border-radius: 20%;
					margin-right: 20px;
				}
			}

			input[type="checkbox"] {
				display: none;

				&:checked + label:before {
					height: 20px;
					width: 20px;
					border: 6px solid rgba(236, 118, 50, 1);
				}
			}
		}

		.card-popup-options {
			display: flex;
			gap: $size-600;
			justify-content: flex-end;
			align-items: center;

			button {
				padding: 0.8rem 3rem;
				color: white;
				background-color: rgba(236, 118, 50, 1);

				&[aria-cancel="true"] {
					background-color: white;
					color: rgba(236, 118, 50, 1);
					border: 1px solid $color-accent;
				}
			}
		}
	}
}
</style>
