						width: 40px;
						height: 35px;
					}

					input {
						border: none;
						text-align: center;
					}

					button {
						background-color: transparent;
						border: none;
						outline: none;

						&:first-child {
							border-right: 1px solid rgba(187, 187, 187, 1);
						}

						&:last-child {
							border-left: 1px solid rgba(187, 187, 187, 1);
						}
