<template>
	<div class="product-navigation">
		<section>
			<div class="main-category">
				<button class="fs-800" @click="toggleNavigation">
					<img src="../assets/Filter.svg" alt="Filter Icon" />
					<span>FILTERS</span>
				</button>
			</div>
		</section>
		<hr />
		<div v-show="mobile">
			<section>
				<div class="main-category">
					<button
						class="fs-800"
						v-on:click="suppliesDropdown = !suppliesDropdown"
					>
						<span>SUPPLIES</span>
						<i class="fa-solid fa-angle-down" v-show="suppliesDropdown"></i
						><i class="fa-solid fa-angle-right" v-show="!suppliesDropdown"></i>
					</button>
				</div>
				<div class="sub-menu" v-show="suppliesDropdown">
					<div
						class="menu-item | category-indent"
						v-for="first in supplies"
						:key="first.id"
					>
						<button
							class="category-indent fs-700"
							v-on:click="first.shown = !first.shown"
						>
							{{ first.headTitle }}
							<i class="fa-solid fa-angle-right" v-show="!first.shown"></i>
							<i class="fa-solid fa-angle-down" v-show="first.shown"></i>
						</button>
						<div
							class="sub-category | text-color"
							v-show="first.shown"
							v-for="second in first.items"
							:key="second.id"
						>
							<!-- TWO SETS SO IT BOLDS WHEN CLICKED -->
							<button
								class="category-indent"
								v-show="!second.shown"
								v-on:click="second.shown = !second.shown"
							>
								{{ second.mainTitle }}
								<i class="fa-solid fa-angle-right" v-show="!second.shown"></i>
								<i class="fa-solid fa-angle-down" v-show="second.shown"></i>
							</button>
							<button
								class="category-indent"
								v-show="second.shown"
								v-on:click="second.shown = !second.shown"
								style="font-weight: bold; text-decoration: underline"
							>
								{{ second.mainTitle }}
								<i class="fa-solid fa-angle-right" v-show="!second.shown"></i>
								<i class="fa-solid fa-angle-down" v-show="second.shown"></i>
							</button>
							<div
								class="category-item"
								v-show="second.shown"
								v-for="third in second.subTitle"
								:key="third.id"
							>
								<!-- <button
								class="sub-category-indent category-indent"
								v-show="second.shown"
								v-for="third in second.subTitle"
								:key="third.id"
							>
								{{ third }}
							</button> -->
								<input
									type="radio"
									:id="third"
									name="userSelect"
									:value="third"
								/>
								<label
									class="sub-category-indent category-indent"
									:for="third"
									>{{ third }}</label
								>
							</div>
						</div>
					</div>
				</div>
			</section>
			<hr />
			<section>
				<div class="main-category">
					<button
						class="fs-800"
						v-on:click="servicesDropdown = !servicesDropdown"
					>
						<span>SERVICES</span>
						<i class="fa-solid fa-angle-down" v-show="servicesDropdown"></i>
						<i class="fa-solid fa-angle-right" v-show="!servicesDropdown"></i>
					</button>
				</div>
				<div class="sub-menu" v-show="servicesDropdown">
					<div
						class="menu-item | category-indent"
						v-for="first in services"
						:key="first.id"
					>
						<button
							class="category-indent fs-700"
							v-on:click="first.shown = !first.shown"
						>
							{{ first.headTitle }}
							<i class="fa-solid fa-angle-right" v-show="!first.shown"></i>
							<i class="fa-solid fa-angle-down" v-show="first.shown"></i>
						</button>
						<div
							class="sub-category | text-color"
							v-show="first.shown"
							v-for="second in first.items"
							:key="second.id"
						>
							<!-- TWO SETS SO IT BOLDS WHEN CLICKED -->
							<button
								class="category-indent"
								v-show="!second.shown"
								v-on:click="second.shown = !second.shown"
							>
								{{ second.mainTitle }}
								<i class="fa-solid fa-angle-right" v-show="!second.shown"></i>
								<i class="fa-solid fa-angle-down" v-show="second.shown"></i>
							</button>
							<button
								class="category-indent"
								v-show="second.shown"
								v-on:click="second.shown = !second.shown"
								style="font-weight: bold; text-decoration: underline"
							>
								{{ second.mainTitle }}
								<i class="fa-solid fa-angle-right" v-show="!second.shown"></i>
								<i class="fa-solid fa-angle-down" v-show="second.shown"></i>
							</button>
							<div
								class="category-item"
								v-show="second.shown"
								v-for="third in second.subTitle"
								:key="third.id"
							>
								<!-- <button
								class="sub-category-indent category-indent"
								v-show="second.shown"
								v-for="third in second.subTitle"
								:key="third.id"
							>
								{{ third }}
							</button> -->
								<input
									type="radio"
									:id="third"
									name="userSelect"
									:value="third"
								/>
								<label
									class="sub-category-indent category-indent"
									:for="third"
									>{{ third }}</label
								>
							</div>
						</div>
					</div>
				</div>
			</section>
		</div>
	</div>
</template>

<script>
export default {
	name: "SideNavigation",
	data() {
		return {
			mobile: true,
			mobileNav: true,
			windowWidth: null,
			suppliesDropdown: false,
			servicesDropdown: false,
			// DATA FOR SUPPLIES
			supplies: [
				{
					headTitle: "Arts & Craft",
					items: [
						{
							mainTitle: "Calling Cards",
							subTitle: ["Calling Cards"],
							shown: false,
						},
						{
							mainTitle: "Document Printing",
							subTitle: [
								"Misallete",
								"Flyers",
								"Booklet",
								"Magazine",
								"Photo Kiosk",
								"Invitations",
								"Gift Cards",
								"Forms",
							],
							shown: false,
						},
						{
							mainTitle: "Matte Stickers",
							subTitle: ["Matte Stickers"],
							shown: false,
						},
						{
							mainTitle: "ID Printing",
							subTitle: ["ID Printing"],
							shown: false,
						},
						{
							mainTitle: "Promotional Products",
							subTitle: ["Promotional Products"],
							shown: false,
						},
						{
							mainTitle: "UV Printing",
							subTitle: ["UV Printing"],
							shown: false,
						},
					],
					shown: false,
				},
				{
					headTitle: "Photocopying",
					items: [
						{
							mainTitle: "Risograph",
							subTitle: ["Risograph", "Carbonized Riso"],
							shown: false,
						},
						{
							mainTitle: "Binding",
							subTitle: [
								"Hardbound",
								"Ringbind",
								"Softbind",
								"Coilbind",
								"Double Loop",
								"Duct Tape",
							],
							shown: false,
						},
						{
							mainTitle: "Photocopying",
							subTitle: ["Photocopying"],
							shown: false,
						},
					],
					shown: false,
				},
			],
			// DATA FOR SERVICES
			services: [
				{
					headTitle: "Digital Printing",
					items: [
						{
							mainTitle: "Accessories",
							subTitle: ["Calling Cards"],
							shown: false,
						},
						{
							mainTitle: "Canvas",
							subTitle: ["Canvas"],
							shown: false,
						},
						{
							mainTitle: "Colored Markers",
							subTitle: [
								"Textile Liner",
								"Coloring Pens",
								"Colored Markers",
								"Colored Pencils",
								"Watercolor Pencils",
							],
							shown: false,
						},
					],
					shown: false,
				},
				{
					headTitle: "Test Title",
					items: [
						{
							mainTitle: "Main Title",
							subTitle: ["Ramona", "Coty", "Gina"],
							shown: false,
						},
					],
				},
			],
		};
	},
	created() {
		window.addEventListener("resize", this.checkScreen);
		this.checkScreen();
	},
	methods: {
		toggleNavigation() {
			this.mobile = !this.mobile;
		},

		checkScreen() {
			this.windowWidth = window.innerWidth;
			if (this.windowWidth <= 1210) {
				this.mobile = false;
				return;
			}

			this.mobile = true;
			this.mobileNav = false;
			return;
		},
	},
};
</script>

<style scoped></style>
